import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Select } from 'antd';
import { homeroomTeachers } from 'dump/data';
import { TableData } from 'components/common/index';
import { getQueryParams } from 'utils/urlUtil';
import FormSerchHomeroomTeacher from './FormSerchHomeroomTeacher';
import { getClassOwners } from '../../../services/classOwner';

const { Option } = Select;

const columns = [
  {
    title: 'Tên lớp',
    dataIndex: 'classId',
    key: 'classId',
  },
  {
    title: 'Số SV',
    dataIndex: 'noOfStudent',
    key: 'noOfStudent',
  },
  {
    title: 'Niên khóa',
    dataIndex: 'courseYear',
    key: 'courseYear',
  },
  {
    title: 'Khóa học',
    dataIndex: 'courseLabel',
    key: 'courseLabel',
  },
  {
    title: 'Tên khoa',
    dataIndex: 'facultyLabel',
    key: 'facultyLabel',
  },
  {
    title: 'Chuyên ngành',
    dataIndex: 'studyFieldLabel',
    key: 'studyFieldLabel',
  },
  {
    title: 'Chủ nhiệm lớp',
    dataIndex: 'teacherName',
    render: () =>
      homeroomTeachers.length >= 1 ? (
        <Fragment>
          <Select defaultValue="lucy" style={{ width: '100%' }}>
            <Option value="jack">Trần Văn A</Option>
            <Option value="lucy">Nguyễn Văn B</Option>
            <Option value="Yiminghe">Trần Văn C</Option>
          </Select>
        </Fragment>
      ) : null,
  },
  {
    title: 'SĐT',
    dataIndex: 'phone',
  },
];

class ListHomeroomTeacherPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };
  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };
  render() {
    return (
      <Card title="Giáo viên chủ nhiệm">
        <Row>
          <Col span={24}>
            <FormSerchHomeroomTeacher />
          </Col>
        </Row>
        <Row>
          <Row>
            <Col span={24}>
              <TableData
                reload={this.state.reload}
                paramSearch={this.state.paramSearch}
                service={getClassOwners}
                columns={columns}
                rowKey="id"
              />
            </Col>
          </Row>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListHomeroomTeacherPage />
  </Page>
);



